.app-header {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: baseline;
          align-items: baseline;
}
.todo-list-item {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.todo-list-text {
  cursor: pointer;
}

.todo-list-text--important {
  color: tomato;
}

.todo-list-text--finished {
  text-decoration: line-through;
}

.todo-list-item-btns button {
  width: 31px;
  height: 31px;
}
.search-panel {
  width: 100%;
}
.item-status-filter__btn--active {
  background-color: blue;
}

.todo-app {
  max-width: 520px;
  width: 90%;
  min-width: 320px;
  margin: 50px auto;
}
